import { Dispatch, SetStateAction } from "react"
import { Box, IconButton } from "@rp/react/web/mui/core"
import { Edit } from "@rp/react/web/mui/icons"
import { SmartGridColumn } from "../WatchlistSmartGrid"

type Props = {
  column: SmartGridColumn
  setSelectedColumn: Dispatch<SetStateAction<SmartGridColumn | null | undefined>>
}

function WatchlistSmartGridHeader({ column, setSelectedColumn }: Props) {
  return (
    <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
      <IconButton
        aria-label="Edit Column"
        size="small"
        sx={{ "& svg": { fontSize: 16 } }}
        onClick={ev => {
          ev.stopPropagation()
          setSelectedColumn(column)
        }}
      >
        <Edit />
      </IconButton>
      {column.title}
    </Box>
  )
}

export { WatchlistSmartGridHeader }
